.btn { width: 70%; border-radius: 999rpx; padding: 18rpx 0; font-size: 26rpx; box-shadow: 0 8rpx 20rpx rgba(0,0,0,0.12); }
.btn.yellow { background: linear-gradient(90deg, #ffc1d1 0%, #ffaac0 100%); color: #ffffff; }
